.projects {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-items: center;
          align-items: center;
  background-image: -webkit-linear-gradient(bisque 5%, teal 35%);
  background-image: linear-gradient(bisque 5%, teal 35%);
  color: bisque;
  min-height: 95vh;
}

.projects p:last-child {
  margin-bottom: 0;
}

.project-card {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 90%;
  border: 1vmin rgb(97, 226, 97) dotted;
  background-color: bisque;
  border-radius: 2vmin;
  height: 50vmin;
  margin: 3vmin;
  color: teal;
  font-size: calc(5px + 1vmin);
}

.project-card iframe {
  object-position: center bottom;
  object-fit: cover;
}

.project-card a {
  display: block;
}

@media only screen and (orientation: landscape) {
  .project-card {
    width: 25%;
  }
}

