.project-card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 90%;
  border: 1vmin rgb(97, 226, 97) dotted;
  background-color: bisque;
  border-radius: 2vmin;
  height: 50vmin;
  margin: 3vmin;
  color: teal;
  font-size: calc(5px + 1vmin);
}

.project-card iframe {
  object-position: center bottom;
  object-fit: cover;
}

.project-card a {
  display: block;
}

@media only screen and (orientation: landscape) {
  .project-card {
    width: 25%;
  }
}
